.Input {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;

}

.Label{
  font-weight: bold;
  display: block;
  margin-bottom: 8px;
  text-align: left;
  margin-top: 15px;
}

.InputEl{
  outline: none;
  border: 1px solid #ccc;
  background-color: #fff;
  font: 'inherit';
  padding: 6px 10px;
  display: block;
  width: 95%;
  box-sizing: border-box;
}

InputEl:focus {
  outline: none;
  background-color: #ccc;
}

.Invalid {
  border: 1px solid red;
  background-color: #FDA49A;
}